<template>
  <div class="obras">
      <v-container v-if="showObras">
        <v-layout row wrap>
          <v-flex sm12 xs12 md12 lg12 class="mt-10">
              <v-card-title class="align-start">
                <v-sheet
                color="brown"
                width="100%"
                class="overflow-hidden mt-n9 transition-swing v-card--material__sheet"
                style="z-index: 2"
                elevation="6"
                max-width="100%"
                rounded
                >
                  <v-theme-provider dark>
                  <slot name="heading" />
                  <div class="py-3">
                    <v-row class="px-5 d-flex">
                      <v-col
                        cols="1"
                        sm="1"
                        md="1"
                        lg="1"
                        xs="1"
                        class="text-left"
                      >
                        <v-btn
                          depressed
                          color="transparent"
                          class="text-left"
                          @click="returnHome"
                        >
                          <v-icon dark small>
                            mdi-arrow-left-bold-circle mdi-36px
                          </v-icon>
                        </v-btn>
                      </v-col>
                      <v-col
                        cols="11"
                        sm="11"
                        md="11"
                        lg="11"
                        xs="11"
                        class="text-center"
                      >
                        <span class="text-h7 white--text  v-card--material__title pr-15">Obras</span>
                      </v-col>
                    </v-row>
                  </div>
                </v-theme-provider>
                </v-sheet>
              </v-card-title>
              <v-card class="card-obras">
                <v-container class="py-0">
                  <v-row class="pt-9 px-5">
                    <template>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="5"
                        xs="5"
                      >
                        <v-text-field
                        v-model="nomePesquisa"
                          clearable
                          label="Pesquisar"
                          append-icon="mdi-magnify"
                        ></v-text-field>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="2"
                        xs="2"
                        style="text-align:right"
                      >
                        <v-btn color="blue-grey" class="white--text" @click="getObras()">Pesquisar</v-btn>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="5"
                        xs="5"
                        style="text-align:right"
                      >
                        <v-btn color="blue" class="white--text" @click="dialogObras = true">Adicionar Obra</v-btn>
                      </v-col>
                    </template>
                  </v-row>
                  <v-row>
                    <template>
                      <v-col
                        cols="12"
                        md="12"
                        lg="12"
                        sm="12"
                        xs="12"
                      >
                        <v-simple-table class="color-table">
                          <template v-slot:default>
                            <thead>
                              <tr>
                                <th class="text-left">
                                  Nome das Obras
                                </th>
                                <th class="text-center">
                                  Qtd. Frente Trabalho Ativos
                                </th>
                                <th class="text-center">
                                  Qtd. Frente Trabalho
                                </th>
                                <!-- <th class="text-center">
                                  Frente Trabalho
                                </th> -->
                                <th class="text-center">
                                  Desativar / Ativar Obra
                                </th>
                                <th class="text-center">
                                  Excluir Obra
                                </th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr
                                v-for="item in desserts"
                                :key="item.obra.nome"
                              >
                                <td>{{ item.obra.nome }}</td>
                                <td class="text-center" style="width: 25%">{{ item.quantidadeFrenteAtivos }}</td>
                                <td class="text-center" style="width: 25%">{{ item.quantidadeFrenteInativo }}</td>
                                <!-- <td class="text-center" style="width: 10%">
                                  <v-btn
                                    width="30px"
                                    height="30px"
                                    fab
                                    dark
                                    small
                                    color="primary"
                                    @click="showFrenteTrabalho(item.obra.nome)"
                                  >
                                    <v-icon dark small>
                                      mdi-dots-vertical
                                    </v-icon>
                                  </v-btn>
                                </td> -->
                                <td class="text-center" style="width: 12%">
                                  <v-btn
                                    width="30px"
                                    height="30px"
                                    fab
                                    dark
                                    small
                                    :color="item.obra.ativo ? 'orange' : 'green'"
                                    @click="alterarStatusObra(item.obra.nome, item.obra.ativo)"
                                  >
                                    <v-icon dark small v-if="item.obra.ativo === false">mdi-play</v-icon>
                                    <v-icon dark small v-else>mdi-pause</v-icon>
                                  </v-btn>
                                </td>
                                <td class="text-center" style="width: 10%">
                                  <v-btn
                                    width="30px"
                                    height="30px"
                                    fab
                                    dark
                                    small
                                    color="red"
                                    @click="deletarObra(item.obra.nome)"
                                  >
                                    <v-icon dark small>
                                      mdi-close
                                    </v-icon>
                                  </v-btn>
                                </td>
                              </tr>
                            </tbody>
                          </template>
                        </v-simple-table>
                      </v-col>
                      </template>
                  </v-row>
                </v-container>
              </v-card>
          </v-flex>
        </v-layout>

        <v-dialog
          v-model="dialogObras"
          transition="dialog-top-transition"
          max-width="600"
        >
          <template>
            <v-card>
              <v-toolbar
                color="primary"
                dark
              >Adicionar Obra</v-toolbar>
              <v-card-text>
                <v-form
                  ref="form"
                >
                    <v-container fluid>
                    <v-row>
                      <v-col
                        cols="12"
                        sm="12"
                      >
                      <v-text-field
                        v-model="novoNomeObra"
                        :rules="rules.name"
                        label="Nome do Obra"
                        required
                        clearable
                      ></v-text-field>
                      </v-col>
                    </v-row>
                    </v-container>
                </v-form>
              </v-card-text>
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="dialogObras = false"
                >Close</v-btn>
                <v-btn
                  color="green"
                  text
                  @click="verificarFormulario()"
                >Salvar</v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>

        <v-snackbar
          v-model="snackbar"
          :color="colorSnackbar"
        >
          {{ mensagem }}

          <template v-slot:action="{ attrs }">
            <v-btn
              color="white"
              text
              v-bind="attrs"
              @click="snackbar = false"
            >
              Close
            </v-btn>
          </template>
        </v-snackbar>

      </v-container>
      <!-- <projetos
        v-if="showFrenteTrabalhos"
        v-on:voltar="returnObras()"
        :nomeObra="nomeObra"
      /> -->

      <div v-if="loader">
        <loader object="#ff9633" color1="#ffffff" color2="#17fd3d" size="5" speed="2" bg="#343a40" objectbg="#999793" opacity="80" name="circular"></loader>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
// import Projetos from '../QcObras/Projetos/Projetos.vue'
export default {
  // components: { Projetos },
  name: 'Obras',
  data: () => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    loader: false,
    desserts: [],
    showObras: true,
    showFrenteTrabalhos: false,
    nomeObra: '',
    novoNomeObra: '',
    snackbar: false,
    dialogObras: false,
    mensagem: '',
    colorSnackbar: '',
    nomePesquisa: '',
    rules: {
      name: [val => (val || '').length > 0 || 'Preencher o Nome da Obra']
    }
  }),
  async created () {
    await this.getObras()
  },
  methods: {
    returnHome () {
      this.$router.push({ name: 'Home' })
    },
    async getObras () {
      this.loader = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}obras`,
        data: { nomePesquisa: this.nomePesquisa ? this.nomePesquisa : '' }
      }
      axios(options)
        .then(res => {
          this.desserts = res.data
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar as Obras'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loader = false
        })
    },

    showFrenteTrabalho (nome) {
      this.showFrenteTrabalhos = true
      this.showObras = false
      this.nomeObra = nome
    },

    returnObras () {
      this.showFrenteTrabalhos = false
      this.showObras = true
      this.getObras()
      // Atualizar a tabela
    },

    async verificarFormulario () {
      if (this.novoNomeObra) {
        this.loader = true

        const options = {
          method: 'POST',
          url: `${this.urlProd}nova-obra`,
          data: { novoNomeObra: this.novoNomeObra }
        }
        axios(options)
          .then(res => {
            this.snackbar = true
            this.mensagem = res.data.mensagem
            this.colorSnackbar = 'green'
            this.dialogObras = false
            this.getObras()
            // Atualizar so a tabela
            this.loader = false
          })
          .catch(err => {
            console.log(err)
            this.snackbar = true
            this.mensagem = 'Erro ao salvar nova Obra'
            this.colorSnackbar = 'red'
            this.loader = false
          })
          .finally(() => {
            this.loader = false
          })
      } else {
        this.snackbar = true
        this.mensagem = 'Preencha o Campo Nome da Obra'
        this.colorSnackbar = 'red'
        this.loader = false
      }
    },

    async deletarObra (nome) {
      this.loader = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}delete-obra`,
        data: { nomeObra: nome }
      }
      axios(options)
        .then(res => {
          console.log(res)
          if (res.data.obra && res.data.obra.length > 0) {
            this.snackbar = true
            this.mensagem = res.data.mensagem + ': ' + res.data.obra
            this.colorSnackbar = 'red'
            this.dialogObras = false
          } else {
            console.log(res.data.mensagem)
            this.snackbar = true
            this.mensagem = res.data.mensagem
            this.colorSnackbar = 'green'
            this.dialogObras = false
            this.getObras()
            // Atualizar so a tabela
          }
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao deletar Obra'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loader = false
        })
    },

    async alterarStatusObra (nome, status) {
      const statusAgora = !status
      console.log(nome, statusAgora)

      this.loader = true

      const options = {
        method: 'POST',
        url: `${this.urlProd}alterar-status-obra`,
        data: {
          nomeObra: nome,
          status: statusAgora
        }
      }
      axios(options)
        .then(res => {
          console.log(res)
          if (res.data.obra && res.data.obra.length > 0) {
            this.snackbar = true
            this.mensagem = res.data.mensagem + ': ' + res.data.obra
            this.colorSnackbar = 'red'
            this.dialogObras = false
          } else {
            console.log(res.data.mensagem)
            this.snackbar = true
            this.mensagem = res.data.mensagem
            this.colorSnackbar = 'green'
            this.dialogObras = false
            this.getObras()
            // Atualizar so a Tabela
          }
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao alterar status da Obras'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loader = false
        })
    }
  }
}
</script>

